      yichangCishu: {
        byType: {
          show: true,
          date: moment().format('MM-DD'),
          chartOption: [
            ['dataset.source', API.cishuByType],
            ['color', [Material.amber.base, Material.indigo.base, Material.teal.base]],
            ['legend.show', true],
            ['legend.selected', {}],
            ['toolbox.show', true],
            ['xAxis.axisLabel.show', true],
            ['yAxis', Array(1).fill({ axisLabel: { show: true }})],
            ['grid.left', '2%'],
            ['grid.bottom', '5%'],
            ['grid.right', '3%'],

            ['series[0].type', 'line'],
            ['series[0].label.show', true],
            ['series[0].smooth', true],
            
          ]
        },
        byDay: {
          show: true,
          type: '异常类型',
          chartOption: [
            ['dataset.source', API.cishuByDay],
            ['color', [Material.amber.base, Material.indigo.base, Material.teal.base]],
            ['legend.show', true],
            ['legend.selected', {}],
            ['toolbox.show', true],
            ['xAxis.axisLabel.show', true],
            ['yAxis', Array(1).fill({ axisLabel: { show: true }})],
            ['grid.left', '2%'],
            ['grid.bottom', '5%'],
            ['grid.right', '3%'],

            ['series[0].type', 'line'],
            ['series[0].label.show', true],
            ['series[0].smooth', true],
            
          ]
        },
        byHour: {
          show: true,
          date: moment().format('MM-DD'),
          chartOption: [
            ['dataset.source', API.cishuByHour],
            ['color', [Material.amber.base, Material.indigo.base, Material.teal.base]],
            ['legend.show', true],
            ['legend.selected', {}],
            ['toolbox.show', true],
            ['xAxis.axisLabel.show', true],
            ['yAxis', Array(1).fill({ axisLabel: { show: true }})],
            ['grid.left', '2%'],
            ['grid.bottom', '5%'],
            ['grid.right', '3%'],

            ['series[0].type', 'line'],
            ['series[0].label.show', true],
            ['series[0].smooth', true],
            
          ]
        }
      },
      yichangShijian: {
        byType: {
          show: true,
          date: moment().format('MM-DD'),
          chartOption: [
            ['dataset.source', API.shijianByType],
            ['color', [Material.indigo.base, Material.teal.base]],
            ['legend.show', true],
            ['legend.selected', {}],
            ['toolbox.show', true],
            ['xAxis.axisLabel.show', true],
            ['yAxis', Array(1).fill({ axisLabel: { show: true }})],
            ['grid.left', '2%'],
            ['grid.bottom', '5%'],
            ['grid.right', '3%'],

            ['series[0].type', 'bar'],
            ['series[0].label.show', true],
            ['series[0].smooth', true],
          ]
        },
        byDay: {
          show: true,
          type: '异常类型',
          chartOption: [
            ['dataset.source', API.shijianByDay],
            ['color', [Material.indigo.base, Material.teal.base]],
            ['legend.show', true],
            ['legend.selected', {}],
            ['toolbox.show', true],
            ['xAxis.axisLabel.show', true],
            ['yAxis', Array(1).fill({ axisLabel: { show: true }})],
            ['grid.left', '2%'],
            ['grid.bottom', '5%'],
            ['grid.right', '3%'],

            ['series[0].type', 'bar'],
            ['series[0].label.show', true],
            ['series[0].smooth', true],
          ]
        },
        byHour: {
          show: true,
          date: moment().format('MM-DD'),
          chartOption: [
            ['dataset.source', API.shijianByHour],
            ['color', [Material.indigo.base, Material.teal.base]],
            ['legend.show', true],
            ['legend.selected', {}],
            ['toolbox.show', true],
            ['xAxis.axisLabel.show', true],
            ['yAxis', Array(1).fill({ axisLabel: { show: true }})],
            ['grid.left', '2%'],
            ['grid.bottom', '5%'],
            ['grid.right', '3%'],

            ['series[0].type', 'bar'],
            ['series[0].label.show', true],
            ['series[0].smooth', true],
          ]
        }
      },
      table: {
        headers: [
          { text: 'Work date', value: 'date' },
          { text: 'Machine name', value: 'machineName' },
          { text: 'Error code', value: 'errorCode' },
          { text: 'Error info', value: 'errorInfo' },
          { text: 'Error rootc', value: 'errorRoot' },
          { text: 'Error action', value: 'errorAction' },
          { text: 'Count', value: 'count' },
          { text: 'Action' }
        ],
        desserts: [
          {
            date: moment().format('YYYY-MM-DD'),
            machineName: 'test machine',
            errorCode: 'M10',
            errorInfo: '',
            errorRoot: '',
            errorAction: '',
            acount: ''
          }
        ]
      }
    };
